import React, { useState } from 'react';
import axios from 'axios';
import './LoginForm.css';
import { API_BASE_URL, ACCESS_TOKEN_NAME } from '../../constants/apiConstants';
import { withRouter } from "react-router-dom";
import { render } from '@testing-library/react';
import { Routes, Route, useNavigate } from 'react-router-dom';

function LoginForm(props) {

    const [state, setState] = useState({
        email: "",
        password: "",
        successMessage: null
    })
    const handleChange = (e) => {
        const { id, value } = e.target
        setState(prevState => ({
            ...prevState,
            [id]: value
        }))
    }

    const handleSubmitClick = async (e) => {
        e.preventDefault();
        console.log(state.email);
        const payload = {
            "id": '',
            "userName": '',
            "email": state.email,
            "password": state.password
        }
        console.log('hey', JSON.stringify(payload))
        var response;
        try {


            response = await axios.post(`http://localhost:8080/client/login`, payload)
            console.log("response:")

            if (response.status == 200) {
                console.log("login successfully:)")
                redirectToHome();
            }
            else if (response.status == 400) {
                console.log("you can't login...")
            }
        }
        catch (error) {
            // console.log("error::" + error.response.data.error)

            if (error.response) {
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
            }
            // console.log("error::" + response.status)
        }



    }
    const redirectToHome = () => {
        props.history.push('/home');
        //props.updateTitle('Home');
        console.log("ittttttt isss redirect to home");
    }
    const redirectToRegister1 = () => {
        props.history.push('/register1');
        //props.updateTitle('Register');
        console.log("ittttttt isss redirect to register");
    }
    return (
        <div className=" col-12 col-lg-4 login-card mt-2 hv-center">
            <div className="out-card">
                <div className="form-login-container">
                    <form>
                        <div className="feild">
                            <p className="para">Email address</p>
                            <input type="email"
                                className="form-control"
                                id="email"
                                aria-describedby="emailHelp"
                                placeholder="Enter email"
                                value={state.email}
                                onChange={handleChange}
                            />
                        </div>

                        <div className="feild">
                            <p className="para">Password</p>
                            <input type="password"
                                className="form-control"
                                id="password"
                                placeholder="Password"
                                value={state.password}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="form-check">
                        </div>
                        <button
                            type="submit"
                            className="btn btn-primary"
                            onClick={handleSubmitClick}
                        >Submit</button>
                    </form>

                    <div className="alert alert-success mt-2" style={{ display: state.successMessage ? 'block' : 'none' }} role="alert">
                        {state.successMessage}
                    </div>
                    <div className="registerMessage">
                        <span>Dont have an account? </span>
                        <br></br>
                        <span className="loginText" onClick={() => redirectToRegister1()}>Register</span>
                    </div>
                    <div className="mt-2">
                        <span className="mainPageText" onClick={() => redirectToHome()}>Click me to go to home page</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(LoginForm);