import React, { useState } from 'react';
import axios from 'axios';
import './ContactUs.css';
import { API_BASE_URL, ACCESS_TOKEN_NAME } from '../../constants/apiConstants';
import { withRouter } from "react-router-dom";
import { render } from '@testing-library/react';
import { Routes, Route, useNavigate } from 'react-router-dom';

function ContactUs(props) {
    const [errorMessage, setErrorMessage] = React.useState("");


    const [state, setState] = useState({
        message: '',
        successMessage: null
    })
    const handleChange = (e) => {
        const { id, value } = e.target
        setState(prevState => ({
            ...prevState,
            [id]: value
        }))
    }

    const handleSubmitClick = async (e) => {
        e.preventDefault();
        console.log(state.email);
        const payload = {
            "message": state.message
        }
        if (state.message === '') {
            setErrorMessage("SORRY, we haven't get your message yet...")
        }
        // const response = await axios.post(`http://localhost:5000/Clients/create_user`, payload)
        console.log('hey', JSON.stringify(payload))
    }

    const redirectToHome = () => {
        props.history.push('/home');
        //props.updateTitle('Home');
        console.log("ittttttt isss redirect to home");
    }
    const redirectToRegister1 = () => {
        props.history.push('/register1');
        //props.updateTitle('Register');
        console.log("ittttttt isss redirect to register");
    }
    return (
        <div className=" col-12 col-lg-4 login-card mt-2 hv-center">
            <div className="out-card">
                <div className="form-login-container">
                    <form>
                        <p className="para">We are glad to know your feedback :</p>
                        <div className="feild">
                            <textarea placeHolder="Write your feedback here :)" id="message" className="message"
                                value={state.message}
                                onChange={handleChange}
                                rows="4" cols="50">

                            </textarea>
                            <div className="feild2">
                                {errorMessage && <div className="error"> {errorMessage} </div>}
                            </div>
                        </div>
                        <div className="feild">
                            <button
                                type="submit"
                                className="btn btn-primary"
                                onClick={handleSubmitClick}
                            >Submit</button>
                        </div>
                    </form>

                    <div className="alert alert-success mt-2" style={{ display: state.successMessage ? 'block' : 'none' }} role="alert">
                        {state.successMessage}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(ContactUs);